body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

header h1 {
  margin: 0;
}

nav a {
  color: #fff;
  text-decoration: none;
  margin: 0 1em;
}

.portfolio div, .blog div, .material div {
  flex: 30%;
  background-color: #eee;
  margin: 1%;
  padding: 1em;
}

.icons a {
  color: #444;
  margin: 0 1em;
  text-decoration: none;
  font-size: 1.5em;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }

  .leftColumn,
  .rightColumn {
    width: 100%;
    padding: 0;
  }

  .project {
    width: 100%;
    margin-bottom: 20px;
  }

  .icons {
    flex-direction: column;
    align-items: center;
  }
}

.header {
  background-color: #C70039;
  color: #fff;
  padding: 1em 0;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
}

.nav {
  background-color: #444;
  color: #fff;
  padding: 1em;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
}

.container {
  padding: 20px;
  width: 100%;
}

.main {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.sidebar {
  flex: 25%;
  background-color: #C70039;
  color: #fff;
  padding: 1em;
  width: 100%;
  box-sizing: border-box;
  margin: 0em;
}



.section {
  margin-bottom: 2em;
  width: 100%;
}

.sectionTitle {
  background-color: #333;
  color: #ffffff;
  padding: 5px 10px;
  border-radius: 5px;
}

.nColumnSection {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.leftColumn {
  background-color: #FBE7E5;
  width: 100%;
}

.rightColumn {
  background-color: #f0f0f0;
  width: 100%;
}

.leftColumnEvents {
  flex: 3;
  margin-right: 10px;
  background-color: #7B8DA3;
  padding: 10px;
  border-radius: 5px;
}

.rightColumnEvents {
  justify-content: center;
  flex: 1;
  margin-right: 10px;
  margin-left: 10px;
  background-color: #f0f0f0;
  padding: 10px;
  margin: 0 10px;
}

.column {
  flex: 1;
  margin: 0 10px;
}

.imageColumn {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 50vh; */
  flex: 1; /* Ocupa uma quarta parte da distribuição horizontal da coluna */
  margin: 0 10px;
}

.textColumn {
  flex: 3; /* Ocupa três quartas partes da distribuição horizontal da coluna */
  margin: 0 10px;
  background-color: #d3d3d3; /* Fundo cinza claro */
  padding: 10px; /* Espaçamento interno */
  border-radius: 5px; /* Bordas arredondadas (opcional) */
}

.project {
  flex: 1;
  margin: 0 10px;
  padding: 5px;
  background-color: #d3d3d3;
  border-radius: 5px;
}

.image {
  width: 100%;
  max-width: 200px;
  height: auto;
  border: 2px solid black;
}

.imageBottle {
  display: flex;
  width: 100%;
  max-width: 100px;
  height: auto;
}

.iconsCalendar {
  display: flex;
  flex-direction: column;
  list-style-type: disc;
  padding-left: 20px;
}

.icons {
  display: flex;
  justify-content: space-around;
  align-items: stretch;
}

.icon {
  width: 50px;
  height: 50px;
}

.iconPDF {
  width: 80px;
  height: 80px;
  justify-content: center;
  align-items: center;
}

.link {
  margin-bottom: 10px;
}

.form {
  display: flex;
  flex-direction: column;
}

.input {
  margin: 0.5em 0;
  padding: 0.5em;
  font-size: 1em;
}

.textarea {
  margin: 0.5em 0;
  padding: 0.5em;
  font-size: 1em;
}

.button {
  padding: 0.5em;
  background-color: #444;
  color: #fff;
  border: none;
}

.imgContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.imgStyle {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.socialMedia {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.socialIcon { /* controla a cor dos icones de redes sociais */
  color: #fff;
}

.bamdeiras { /* controla o posicionamento da caixa de bamdeiras de visitantes */
  text-align: center;
  margin-top: 1rem;
}

.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 1em 0;
  width: 100%;
  box-sizing: border-box;
}

/* ################################################################################## */
/* O codigo embaixo é para mexer no formato de comentarios */
.form_wrapper { /* controla o posisionamento dos elementos dentro de section */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}
form { /* controla o formato do texto fora das caixas */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: right;
}
input { /* controla a apariência das caixas Nome e Email */
  width: 60vw;
  min-width: 12rem;
  height: 50px;
  margin: 10px auto;
  padding: 20px;
  border: none;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: inset 9px 9px 19px #d9d9d9, inset -9px -9px 19px
              #d1cccc;
}
/* label {
  align-self: flex-start;
  margin-top: 1rem;
} */
/* *:focus {
  outline: none;
  box-shadow: 0 5px 10px rgba(170, 0, 255, 0.2);
} */
button { /* controla o estilo e as propiedades do botão */ 
  width: 60vw;
  margin-top: 0rem;
  padding: 6px;
  font-size: 1rem;
  cursor: pointer;
  background: transparent;
  border: none;
  border-radius: 7px;
  background: linear-gradient(145deg, #60b8eb, #9bc5ad);
  box-shadow: 9px 9px 19px #888686, -9px -9px 19px #f0ffff;
}
/* button:hover {
  background: linear-gradient(145deg, #bce6d2, #e0fff9);
  box-shadow: 9px 9px 19px #b2d9c6, -9px -9px 19px #f0ffff;
} */
textarea { /* controla o estilo da caixa da Menssagem */
  min-width: 60vw;
  margin: 10px auto;
  padding: 20px;
  border: none;
  border-radius: 15px;
  background: #ffffff;
  box-shadow: inset 9px 9px 19px #d9d9d9, inset -9px -9px 19px
              #c5c3c3;
}
